import { list, forceLogout } from "@/api/monitor/online";
export default {
  name: "Online",
  data: function data() {
    return {
      // 遮罩层
      loading: true,
      // 总条数
      total: 0,
      // 表格数据
      list: [],
      pageNum: 1,
      pageSize: 10,
      // 查询参数
      queryParams: {
        ipaddr: undefined,
        userName: undefined
      }
    };
  },
  created: function created() {
    this.getList();
  },
  methods: {
    /** 查询登录日志列表 */
    getList: function getList() {
      var _this = this;

      this.loading = true;
      list(this.queryParams).then(function (response) {
        _this.list = response.rows;
        _this.total = response.total;
        _this.loading = false;
      });
    },

    /** 搜索按钮操作 */
    handleQuery: function handleQuery() {
      this.pageNum = 1;
      this.getList();
    },

    /** 重置按钮操作 */
    resetQuery: function resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },

    /** 强退按钮操作 */
    handleForceLogout: function handleForceLogout(row) {
      var _this2 = this;

      this.$confirm('是否确认强退名称为"' + row.userName + '"的数据项?', "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(function () {
        return forceLogout(row.tokenId);
      }).then(function () {
        _this2.getList();

        _this2.msgSuccess("强退成功");
      });
    }
  }
};