import { listDbTable, importTable } from "@/api/tool/gen";
export default {
  data: function data() {
    return {
      // 遮罩层
      visible: false,
      // 选中数组值
      tables: [],
      // 总条数
      total: 0,
      // 表数据
      dbTableList: [],
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        tableName: undefined,
        tableComment: undefined
      }
    };
  },
  methods: {
    // 显示弹框
    show: function show() {
      this.getList();
      this.visible = true;
    },
    clickRow: function clickRow(row) {
      this.$refs.table.toggleRowSelection(row);
    },
    // 多选框选中数据
    handleSelectionChange: function handleSelectionChange(selection) {
      this.tables = selection.map(function (item) {
        return item.tableName;
      });
    },
    // 查询表数据
    getList: function getList() {
      var _this = this;

      listDbTable(this.queryParams).then(function (res) {
        if (res.code === 200) {
          _this.dbTableList = res.rows;
          _this.total = res.total;
        }
      });
    },

    /** 搜索按钮操作 */
    handleQuery: function handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },

    /** 重置按钮操作 */
    resetQuery: function resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },

    /** 导入按钮操作 */
    handleImportTable: function handleImportTable() {
      var _this2 = this;

      importTable({
        tables: this.tables.join(",")
